/*  Placeholder Mixin
*
*   No Params
*
*   Example Usage:
*
*     input[placeholder] {
*       @include placeholder {
*         color: red;
*       }
*     }
*
*/
/*  PX to REM Conversion
*
*   @param {Pixel} $px - Unitless pixel value to be converted into rems.
*
*   Example Usage:
*
*     margin-bottom: rem(20);
*
*/
/*
 * 3.1 Root
 */
* {
  box-sizing: border-box;
}

html {
  font-size: 100%;
  line-height: 1.5em;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  min-height: 100%;
  scroll-behavior: smooth;
}

@media print {
  html {
    font-size: 12pt;
  }
}

body {
  min-width: 20rem;
  font-size: 1rem;
  line-height: 1.5;
  font-family: "Poppins-Regular", sans-serif;
  color: #333;
  position: relative;
}

body .site-content {
  background: #fcfcfc;
  overflow: hidden;
}

body .menu-overlay {
  background-color: rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  z-index: 0;
}

body.overlayed {
  overflow: hidden;
}

body.overlayed .menu-overlay {
  z-index: 2;
}

/*
 * 3.2 Typography
 */
p {
  margin: 0 0 0.9375rem;
}

a {
  color: #2cac4a;
}

a:hover {
  text-decoration: none;
}

h1 {
  margin: 0 0 1.375rem;
  color: #333;
  font-family: "Poppins-Medium", sans-serif;
}

h2 {
  margin: 0 0 1.375rem;
  color: #333;
  font-family: "Poppins-Medium", sans-serif;
}

h3 {
  margin: 0 0 1.375rem;
  color: #333;
  font-family: "Poppins-Medium", sans-serif;
}

h4 {
  margin: 0 0 1.375rem;
  color: #333;
  font-family: "Poppins-Medium", sans-serif;
}

h5 {
  margin: 0 0 1.375rem;
  color: #333;
  font-family: "Poppins-Medium", sans-serif;
}

h6 {
  margin: 0 0 1.375rem;
  color: #333;
  font-family: "Poppins-Medium", sans-serif;
}

h1 {
  font-size: 2.625rem;
}

h2 {
  font-size: 2.125rem;
}

h3 {
  font-size: 1.5rem;
}

h4 {
  font-size: 1.25rem;
}

h5 {
  font-size: 1.125rem;
}

h6 {
  font-size: 1rem;
}

.page-title {
  font-size: 1rem;
  margin-bottom: 0;
}

/*
 * 3.3 Form base styles
 */
.woocommerce form .form-row input.input-text,
.woocommerce form .form-row textarea,
input[type='text'],
input[type='password'],
input[type='email'],
input[type='search'],
input[type='tel'],
textarea {
  font-size: 0.875rem;
  padding: 0.75rem 1rem 0.8125rem;
  background-color: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 6px;
  box-sizing: border-box;
  box-shadow: none;
  color: #333;
  display: inline-block;
  line-height: 1;
  margin: 0;
  max-width: 100%;
  width: 100%;
  -webkit-appearance: none;
}

.woocommerce form .form-row input.input-text + span, .woocommerce form .form-row textarea + span,
input[type='text'] + span,
input[type='password'] + span,
input[type='email'] + span,
input[type='search'] + span,
input[type='tel'] + span,
textarea + span {
  font-size: 0.75rem;
  color: #bdbdbd;
}

.woocommerce form .form-row input.input-text::-webkit-input-placeholder,
.woocommerce form .form-row textarea::-webkit-input-placeholder,
input[type='text']::-webkit-input-placeholder,
input[type='password']::-webkit-input-placeholder,
input[type='email']::-webkit-input-placeholder,
input[type='search']::-webkit-input-placeholder,
input[type='tel']::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #bdbdbd;
}

.woocommerce form .form-row input.input-text::-moz-placeholder,
.woocommerce form .form-row textarea::-moz-placeholder,
input[type='text']::-moz-placeholder,
input[type='password']::-moz-placeholder,
input[type='email']::-moz-placeholder,
input[type='search']::-moz-placeholder,
input[type='tel']::-moz-placeholder,
textarea::-moz-placeholder {
  color: #bdbdbd;
}

.woocommerce form .form-row input.input-text:-ms-input-placeholder,
.woocommerce form .form-row textarea:-ms-input-placeholder,
input[type='text']:-ms-input-placeholder,
input[type='password']:-ms-input-placeholder,
input[type='email']:-ms-input-placeholder,
input[type='search']:-ms-input-placeholder,
input[type='tel']:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: #bdbdbd;
}

.woocommerce form .form-row input.input-text::placeholder,
.woocommerce form .form-row textarea::placeholder,
input[type='text']::placeholder,
input[type='password']::placeholder,
input[type='email']::placeholder,
input[type='search']::placeholder,
input[type='tel']::placeholder,
textarea::placeholder {
  color: #bdbdbd;
}

.woocommerce #respond input#submit,
.woocommerce a.button,
.woocommerce button.button,
.woocommerce input.button,
input[type='submit'],
input[type='button'] {
  font-size: 1.125rem;
  font-weight: 600;
  border: 0;
  border-radius: 6px;
  background: #2cac4a;
  color: #fff;
}

.woocommerce #respond input#submit:hover,
.woocommerce a.button:hover,
.woocommerce button.button:hover,
.woocommerce input.button:hover,
input[type='submit']:hover,
input[type='button']:hover {
  background: #139231;
  color: #fff;
}


label {
  font-size: 0.75rem;
  color: #333;
  letter-spacing: .6px;
  display: block;
  position: relative;
}


label .form-required {
  color: #d20000;
}


input[type='checkbox'] {
  display: none;
}


input[type='checkbox'] + label::before {
  width: 1.125rem;
  height: 1.125rem;
  margin: -0.0625rem 0.625rem 0 0;
  content: '';
  cursor: pointer;
  background-image: url(../../images///checkbox.png);
  background-position: 0 bottom;
  background-repeat: no-repeat;
  display: inline-block;
  vertical-align: middle;
}


input[type='checkbox']:checked + label::before {
  background-position: 0 0;
}

fieldset {
  margin: 0 0 1rem;
  border: 1px solid #e0e0e0;
  border-radius: 6px;
  background: #fcfcfc;
}

/*
 * 3.4 Default list styles
 */
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

/*
 * 3.5 Media elements styles
 */
img,
media {
  max-width: 100%;
  vertical-align: top;
  height: auto;
}

#map_canvas img {
  max-width: none;
}

/*
 * 3.7 Layouts styles
 */
.site-wrapper {
  overflow: hidden;
  position: relative;
}

.container {
  padding: 5.75rem 0.625rem 1.875rem;
  margin: 0 auto;
  max-width: 1150px;
}

@media (max-width: 767px) {
  .container {
    padding: 4.375rem 0.625rem 2.5rem;
  }
}

.short-info {
  font-size: 1.125rem;
  max-width: 47.875rem;
  margin: 0 auto 5.375rem;
  line-height: 1.5;
  text-align: center;
}

@media (max-width: 767px) {
  .short-info {
    margin-bottom: 3.75rem;
  }
}

/*
 * 3.8 Basic table styles
 */
table {
  width: 100%;
}

.page-wrapper {
  padding: 0 1.25rem 4.375rem;
}

.content-area {
  max-width: 71.875rem;
  padding: 0.9375rem 1.25rem 4.0625rem;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .content-area {
    padding: 0.9375rem 1.25rem 3.125rem;
  }
}

/*
 * 4.8 Header section styles
 */
@media (min-width: 768px) {
  .header {
    background: #fff;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.02);
    position: relative;
  }
}

.main_navigation {
  max-width: 71.875rem;
  padding: 0 1.25rem;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .main_navigation {
    position: relative;
  }
}

.nav-wrapper {
  padding: 1.1875rem 0 0.9375rem;
  display: flex;
  justify-content: space-between;
}

@media (min-width: 768px) {
  .nav-wrapper {
    padding: 1.125rem 0 1.0625rem;
    justify-content: flex-start;
  }
}

.nav-wrapper .logo-wrapper {
  width: 6.9375rem;
  height: 3.125rem;
}

@media (min-width: 768px) {
  .nav-wrapper .logo-wrapper {
    margin: 0.4375rem auto 0 0;
    width: 11.875rem;
    height: 2.375rem;
  }
}

.nav-wrapper .logo-wrapper .custom-logo-link {
  display: block;
  width: 100%;
  height: 100%;
}

.nav-wrapper .logo-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.nav-wrapper .nav-buttons {
  display: none;
  width: 100%;
  margin-right: 8.6%;
}

@media (min-width: 768px) {
  .nav-wrapper .nav-buttons {
    max-width: 17.5rem;
    display: flex;
  }
}

@media (min-width: 1025px) {
  .nav-wrapper .nav-buttons {
    max-width: 23.25rem;
  }
}

.nav-wrapper .header_buttons {
  display: flex;
}

@media (min-width: 768px) {
  .nav-wrapper .header_buttons {
    padding: 0.8125rem 0 0;
    margin-left: auto;
  }
}

.nav-wrapper .header_buttons .widget_icl_lang_sel_widget,
.nav-wrapper .header_buttons .wpml-ls-legacy-dropdown-click {
  width: 3.75rem;
}

.nav-wrapper .header_buttons .widget_icl_lang_sel_widget a.wpml-ls-item-toggle,
.nav-wrapper .header_buttons .wpml-ls-legacy-dropdown-click a.wpml-ls-item-toggle {
  border: 0;
}

@media (min-width: 768px) {
  .nav-wrapper .header_buttons .widget_icl_lang_sel_widget a.wpml-ls-item-toggle,
  .nav-wrapper .header_buttons .wpml-ls-legacy-dropdown-click a.wpml-ls-item-toggle {
    font-size: 1.5rem;
    padding-top: 0;
    letter-spacing: .75px;
    font-weight: 600;
  }
  .nav-wrapper .header_buttons .widget_icl_lang_sel_widget a.wpml-ls-item-toggle::after,
  .nav-wrapper .header_buttons .wpml-ls-legacy-dropdown-click a.wpml-ls-item-toggle::after {
    right: 0.4375rem;
    border-width: 5px;
    border-top-width: 4px;
  }
}

.nav-wrapper .header_buttons .widget_icl_lang_sel_widget .wpml-ls-sub-menu,
.nav-wrapper .header_buttons .wpml-ls-legacy-dropdown-click .wpml-ls-sub-menu {
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.1);
}

.nav-wrapper .header_buttons .widget_icl_lang_sel_widget > ul,
.nav-wrapper .header_buttons .wpml-ls-legacy-dropdown-click > ul {
  padding: 0;
}

.nav-wrapper .header_buttons .cart-button {
  width: 1.375rem;
  height: 1.375rem;
  position: relative;
  background: url("../../img/ico/cart.svg") center no-repeat;
  display: block;
}

@media (min-width: 768px) {
  .nav-wrapper .header_buttons .cart-button {
    display: none;
  }
}

.nav-wrapper .header_buttons .cart-button span {
  width: 0.6875rem;
  height: 0.6875rem;
  font-size: 0.5rem;
  background: #2cac4a;
  border-radius: 8px;
  position: absolute;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-weight: 700;
  text-align: center;
  top: -4px;
  right: -4px;
}

.nav-wrapper .header_buttons .open-search {
  display: none;
}

@media (min-width: 768px) {
  .nav-wrapper .header_buttons .open-search {
    margin: 0 0.3125rem 0 0.625rem;
    width: 1.4375rem;
    height: 1.4375rem;
    display: block;
    background: none;
    border: 0;
  }
}

@media (min-width: 768px) {
  .widget_search {
    top: 1.25rem;
    right: 1.25rem;
    position: absolute;
    background: #fff;
    max-width: 0;
    width: 100%;
    overflow: hidden;
    transition: max-width .2s ease;
  }
  .widget_search.active {
    max-width: 18.375rem;
  }
}

.widget_search .widgettitle,
.widget_search .search-submit {
  display: none;
}

.widget_search label {
  padding: 0;
}

.widget_search .search-field {
  height: 2.25rem;
  padding: 0 0 0 2.625rem;
